<template>
    <div class="h-full">
        <TopBar></TopBar>
        <section class="main-container p-20 fn-clear">
            <router-view></router-view>
        </section>
    </div>
</template>

<script>
    export default {
        name: 'Index',
        mixins: [],
        components: {},
        data() {
            return {}
        },
        beforeRouteEnter(to, from, next) {
            next();
        },
        created() {
        },
        mounted() {
            this.calculateSize();
            window.onresize = () => {
                this.calculateSize();
            }
        },
        computed: {},
        methods: {
            calculateSize() {
                const clientW = window.innerWidth;
                const clientH = window.innerHeight;
                // console.log(clientW);
                document.querySelector('.main-container').style.minHeight = (clientH - 75) + 'px';
            }
        },
        filters: {},
        watch: {},
        destroyed() {
        },
        beforeRouteLeave(to, from, next) {
            next();
        }
    }
</script>

<style lang="scss" scoped>
    .main-container {
        /*min-height: calc( 100% - 75px );*/
        overflow: hidden;
    }
</style>



